@import "../../utils/mixin.scss";
@import "../../utils/fonts.scss";

@include fonts;

.header_error {
    background: #E5E5E5;
    padding-bottom: 108px;

    .img_container {
        padding-top: 87px;
        padding-bottom: 70px;

        img {
            width: 100%;
        }
    }

    .error_title {
        margin-bottom: 17px;
        font-weight: 600;
        font-size: 40px;
        line-height: 70%;
        text-align: center;
        color: #434343;
    }

    .error_text {
        font-weight: 400;
        font-size: 22px;
        line-height: 127%;
        text-align: center;
        color: #434343;
        margin-bottom: 45px;
    }

    .error {
        display: block;
        width: 299px;
        height: 70px;
        margin: 0 auto;
    }

    button {

        font-weight: 600;
        font-size: 22px;
        line-height: 136%;
        text-align: center;

    }
}